@import "fontawesome.min.css";
@import "all.min.css";
* {
  box-sizing: border-box;
  direction: rtl;
}

body {
  font-family: "Tajawal", sans-serif !important;
}
body.ubuntu {
  font-family: "Ubuntu", sans-serif;
}
body.roboto {
  font-family: "Roboto", sans-serif;
}
body.open {
  font-family: "Open Sans", sans-serif;
}
body.source {
  font-family: "Source Sans Pro", sans-serif;
}

.page {
  min-height: 100vh;
}
.page .content-area {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: 0.3s;
  will-change: width;
  background-color: #f2f1f2;
  float: right;
}
.page .content-area.no-sidebar {
  width: 100%;
}
.page .page-head {
  margin-top: 20px;
  padding: 20px;
}
.page .page-head .page-title {
  font-weight: bold;
  color: #27ae60;
}
.setting-box {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
  background-color: #fff;
  z-index: 1000;
  transition: 0.3s;
  padding: 20px;
}
.setting-box .option-title {
  font-weight: bold;
  color: #666;
  margin: 10px 0 15px;
}
.setting-box .toggle-setting {
  position: absolute;
  left: -48px;
  top: 100px;
  background-color: #fff;
  padding: 15px;
  font-size: 22px;
  border-radius: 5px 0 0 5px;
  cursor: pointer;
}
.setting-box.hide-setting {
  right: -250px;
}
.setting-box .option-box {
  margin: 10px;
  border: solid 1px;
}
.setting-box .color-options ul li:nth-child(1) {
  background-color: #27ae60;
}
.setting-box .color-options ul li:nth-child(2) {
  background-color: #2980b9;
}
.setting-box .color-options ul li:nth-child(3) {
  background-color: #c0392b;
}
.setting-box .color-options ul li:nth-child(4) {
  background-color: orange;
}
.setting-box .color-options ul li:nth-child(5) {
  background-color: purple;
}
.setting-box .color-options ul li:nth-child(6) {
  background-color: black;
}
.setting-box .color-options ul li {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  opacity: 0.5;
  display: inline-block;
  cursor: pointer;
}
.setting-box .color-options ul li.active {
  opacity: 1;
}
.setting-box .font-options select {
  text-transform: capitalize;
  cursor: pointer;
}

