/* End Landing Page */
.landing-page {
  min-height: 100vh;
  background-image: url("../images/background-4.jpg");
  background-size: cover;
  position: relative;
  transition: 1s;
}
.landing-page .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.landing-page .header-container {
  border-color: transparent transparent #fff;
  border-width: 1px;
  border-style: solid;
}
.landing-page .header-area {
  position: relative;
  color: #fff;
  display: flex;
  padding: 10px;
  font-family: "Gulzar", serif;
}
.landing-page .header-area .logo {
  width: 300px;
  padding: 15px;
  font-weight: bold;
}
.landing-page .header-area .links {
  list-style: none;
  width: 100%;
  padding-right: 20px;
  padding-left: 0;
}
.landing-page .header-area .links li {
  display: inline-block;
  margin-left: 10px;
}
.landing-page .header-area .links li span {
  color: #fff;
  text-decoration: none;
  transition: 0.3s;
  cursor: pointer;
}
.landing-page .header-area .links li span:hover {
  color: #ff9800;
}
.landing-page .header-area .links li span.active {
  color: #ff9800;
}
.landing-page .header-area .links-container {
  margin-top: 10px;
  width: 100%;
  text-align: left;
}
.landing-page .header-area .toggle-menu {
  border: none;
  background: none;
  width: 40px;
  margin-top: 15px;
  cursor: pointer;
  display: none;
}
.landing-page .header-area .toggle-menu span {
  height: 4px;
  margin-bottom: 8px;
  background-color: #fff;
  display: block;
}
.landing-page .header-area .toggle-menu.menu-active::before {
  content: "";
  position: relative;
  border: 10px solid #fff;
  border-color: transparent transparent #fff transparent;
  left: 0px;
  bottom: -35px;
}
.landing-page .intro-text {
  position: absolute;
  top: 50%;
  font-family: "Reem Kufi Fun", sans-serif !important;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  width: 92%;
}
.landing-page .intro-text h1 {
  font-size: 34px;
  margin: 0 0 20px;
}
.landing-page .intro-text .qotet-text {
  line-height: 1.6;
  font-size: 20px;
  font-family: "Reem Kufi Fun", sans-serif !important;
}
@media (max-width: 575px) {
  .landing-page .intro-text h1 {
    font-size: 26px;
  }
}
@media (max-width: 991px) {
  .landing-page .header-area .links {
    display: none;
  }
  .landing-page .header-area .links.open {
    position: absolute;
    left: 0;
    top: 75px;
    background-color: #fff;
    border-radius: 4px;
    width: 100%;
    padding: 10px;
    display: block;
    text-align: left;
    z-index: 10;
  }
  .landing-page .header-area .links.open li {
    margin: 10px;
    display: block;
  }
  .landing-page .header-area .links.open li span {
    font-weight: bold;
    color: #ff9800;
  }
  .landing-page .header-area .toggle-menu {
    display: inline-block;
  }
}/*# sourceMappingURL=main.css.map */